/*-----------------------------------------------------------------------------------*/
/*	02. BLOG
/*-----------------------------------------------------------------------------------*/
.blog{
	.card{
		margin-bottom: 30px;
	}
	.card-deck{
		margin-bottom: 20px;
	}
	&.no-sidebar {
		margin: 0 auto;
    	width: 100%;
	    @media (min-width: 768px){
	    	width: 720px;
	    }
	}
	.card-block{
		.media{
			.media-left{
				padding-right: 20px;
			}
			img{
				width: auto;
			}
		}
	}
}

