.mfp-container{
	.mfp-close{
		top: -10px;
		right: 0;
		left: auto;
	}
	.mfp-header{
		background: rgba(0,0,0,0.1);
		padding: 0 0 15px 0;
		h4{
			text-transform: uppercase;
			color: #fff;
			margin-bottom: 0;
		}	
	}	
	.mfp-figure{
		&:after{
			box-shadow: none;
			background: none;
		}	
		img{
			&.mfp-img {
			    padding: 0;
			}
		}
	}
	.mfp-title {
		line-height: 24px;
		.meta{
			line-height: normal;
			margin-top: 5px;
		}
		.share{
			margin-top: 15px;
		}
	}
}

.mfp-iframe-holder{
	.mfp-content{
		top: 30px;
	}
	.mfp-iframe-scaler{
		iframe{
			&:before{
				content: '';
				position: relative;
				display: inline-block;
				margin-top: 50px;
			}	
		}
		.mfp-header {
			position:absolute;
			left: 0;
			top: -90px;
			color: #FFF;
			width: 100%;
		}
	}
}

