/*	BUTTON
/*-----------------------------------------------------------------------------------*/
.btn{
    background: $button-bg-color;
}

.btn{
    color: #FFF !important;
    background: $button-bg-color;
    border: 0;
    margin-bottom: 10px;
    margin-right: 4px;
    font-size: 12px;
    line-height: 1;
    padding: 14px 20px 13px;
    font-weight: 700;
    text-shadow: none;
    text-transform: none;
    transition: all 150ms ease-in;
    box-shadow: none;
    border-radius: 0;
    text-transform: uppercase;
    display: inline-block;
    font-family: 'Montserrat', sans-serif;
}