/*  ICON
/*-----------------------------------------------------------------------------------*/

.icon-video{
	&:after{
		content: "";
		display: block;
		border: 3px solid #fff;
		background-color: rgba(0,0,0,0.5);
		border-radius: 100%;
	    height: 50px;
	    width: 50px;
	}
	&:before{
		content: '\f04b';
		font-family: 'FontAwesome';
		font-size: 18px;
		color: #fff;
		position: absolute;
		top: 50%;
		left: 50%;
		margin-top: -11px;
		margin-left: -5px;
	}
}

.icon-image{
	&:after{
		@extend .icon-video:after;
	}
	&:before{
		content: '\f030';
		font-family: 'FontAwesome';
		font-size: 18px;
		color: #fff;
		position: absolute;
		top: 50%;
		left: 50%;
		margin-top: -11px;
		margin-left: -9px;
	}
}