/*-----------------------------------------------------------------------------------*/
/*  OWLCAROUSEL
/*-----------------------------------------------------------------------------------*/

.blog-carousel,
.media-carousel{
	box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
	.caption-overlay{		
		.caption{
			z-index: 1;
			width: 100%;
			text-align: center;
			display: inline;
			a{
				pointer-events: all;
			}
			&.bottom-left{
				bottom: 0;
				left: 0;
				text-align: left;
			}
			.layer {
				width: 100%;
				display: inline-block;
				box-shadow: none;
				padding: 30px;
			}
			.post-title{
				margin-bottom: 0;
				a{
					color: #FFF;
					&:hover{
						text-decoration: none;
					}					
				}
			}
			.share{
				margin-top: 15px;
				.share-link {
					&:before {
						background-color: rgba(0,0,0,0.8);
					}
				}
				.icon-square{
					background-color: rgba(0,0,0,0.8);
				}
			}
			.meta{
				line-height: normal;
				color: #ccc;
				a{
					@extend .meta;
				}
			}
		}
	}
}

.owl-carousel{
	position: relative;
	.caption-overlay{
		.caption{
			.post-title{
				font-size: 18px;
			}
		}
	}
}

.owl-nav{
	.owl-prev{
		z-index: 100;
		cursor: pointer;
		position: absolute;
		transition: all 150ms ease-in;
		background: none;
		color: rgba(255,255,255,0.5);
		font-size: 55px;
		width: auto;
		height: auto;
		&:before {
	    	font-family: FontAwesome;
		    font-style: normal;
		    font-weight: normal;
		    speak: none;
		    display: inline-block;
		    text-decoration: inherit;
		    text-align: center;
		}
	} 
	.owl-prev{
		color: rgba(255, 255, 255, 0.8);
		left: 15px;
		&:before {
			content: "\f104";
		}
	}
	.owl-next{
		@extend .owl-prev;
	}
	.owl-next{
		left: auto;
    	right: 15px;
		&:before {
			content: "\f105";
		}
	}
}

.owl-theme{
	.owl-controls{
		margin-top: 10px;
	    text-align: center;
	    -webkit-tap-highlight-color: transparent;	    
	}
	.owl-nav {
	    position: absolute;
	    top: 50%;
	    margin-top: -13px;
	    width: 100%;
	}
}

.media-carousel{
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
	.icon-video,
	.icon-image{
		left: 50%;
		margin: -26px 0 0 -26px;
		position: absolute;
		top: 50%;
	}
	a{
		&:focus{
			outline: none;
		}
	}
}