body{
 	  font-family: "Roboto", sans-serif;
  	font-weight: normal;
  	line-height: 25px;
  	font-size: 14px;
    color: #82826d;	
}


h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
    color: #fff;
    margin-bottom: 10px;
    font-weight: 700;
    letter-spacing: -0.5px;
    line-height: 20px;
}

h1 {
    font-size: 28px;
    line-height: 32px;
    margin-bottom: 15px;
}
h2 {
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 15px;
}
h3 {
    font-size: 18px;
    line-height: 22px;
}
h4 {
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 7px;
    letter-spacing: -0.2px;
}
h5 {
  font-size: 15px;
  line-height: 19px;
  letter-spacing: -0.2px;
}
h6 {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.2px;
}