/*-----------------------------------------------------------------------------------*/
/*	THUMBNAIL OVERLAY
/*-----------------------------------------------------------------------------------*/

figure {
    display: block;
    overflow: hidden;
    position: relative;
    margin-bottom: 20px;
    img {
	    display: block;
	    max-width: 100%;
	}
}

.caption-overlay{
    position: relative;
	.caption {
		position: absolute;
		padding: 0;
		width: 100%;
		pointer-events: none;
		bottom: 40px;
		right: 0px;
		text-align: right;
	}
}