.dark-wrapper {
    background: $primary-color-dark;
}

.light-wrapper{
    background-color: $primary-color-light;
}

.container {
    padding-right: 0;
    padding-left: 0;
    @media (max-width: 767px){
        padding-right: 20px;
        padding-left: 20px;
    }
}

body,
li,
address {
    line-height: 24px
}

ul,
ol {
    margin-bottom: 15px
}

ul{
    &.circled {
        padding: 0;
        list-style: none;
        li:before {
            content: '●';
            margin-right: 10px;
            font-weight: normal;
            vertical-align: top;
            display: inline-block;
            color: #70aed2;
            font-size: 11px;
        }
    }
}

.hidden {
    display: none !important;
}

input,
button,
select,
textarea {
    font-family: inherit;
    font-size: 18px;
    font-weight: normal;
}

.inner {
    padding-top: 80px;
    padding-bottom: 80px;
}

.inner2 {
    padding-top: 70px;
    padding-bottom: 70px;
}

.inner-top{
    padding-top: 80px;
}

#map {
    height: 400px;
}

.offset {
    @media (max-width: 991px){
        display: none;
    }
}

.section-title {
    font-size: 28px;
    line-height: 28px;
    margin-bottom: 30px;
    text-transform: uppercase;
    color: #fff;
    font-weight: 500;
}

.meta{
    font-size: 11px;
    text-transform: uppercase;
    span{
        color: #F2B705;
        &:before{
            content: "|";
            padding: 0 5px;
            
        }
        &:first-child{
            &:before{
                display: none;
            }
        }
    }
    a{
        color: #F2B705;
    }
}

.dark-wrapper{
    h3{
        color: #fff;
    }
}
.light-wrapper{
    h3{
        color: #fff;
    }
}

.img-overlay{
    &:before {
        bottom: 0;
        content: "";
        display: block;
        height: 70%;
        width: 100%;
        position: absolute;
        z-index: 1;
        background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 40%, rgba(0, 0, 0, 0.7) 100%);
        /* FF3.6+ */
        background: -webkit-gradient(linear, left top, left bottom, color-stop(40%, rgba(0, 0, 0, 0)), color-stop(100%, rgba(0, 0, 0, 0.7)));
        /* Chrome,Safari4+ */
        background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 40%, rgba(0, 0, 0, 0.7) 100%);
        /* Chrome10+,Safari5.1+ */
        background: -o-linear-gradient(top, rgba(0, 0, 0, 0) 40%, rgba(0, 0, 0, 0.7) 100%);
        /* Opera 11.10+ */
        background: -ms-linear-gradient(top, rgba(0, 0, 0, 0) 40%, rgba(0, 0, 0, 0.7) 100%);
        /* IE10+ */
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 40%, rgba(0, 0, 0, 0.7) 100%);
        /* W3C */
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#b3000000', GradientType=0);
        /* IE6-9 */
        /* responsive portrait phone */
    }
}




