/*-----------------------------------------------------------------------------------*/
/*  NAVBAR
/*-----------------------------------------------------------------------------------*/

.navbar {
    border: 0;
    border-radius: 0;
    min-height: inherit;
    width: 100%;
    height: 90px;
    padding: 0;
    background: $primary-color;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
    @media (min-width: 992px) { 
        display: table;
        padding: 0 40px;
    }
    @media (max-width: 991px){
        position: relative;
        height: auto;
    } 
    .basic-wrapper{        
        @media (max-width: 991px){
            width: 720px;
            margin: 0 auto;
            display: table;
            height: 90px;
        }
        @media (max-width: 767px){
            width: 100%;
            padding-left: 20px;
            padding-right: 20px;
            margin: 0 auto;
        }
    }
    .navbar-header {
        @media (min-width: 992px) { 
            display: table-cell !important;
            vertical-align: middle;
            float: none !important;
        }
        @media (max-width: 991px){
            float: left;
            width: 100%;
        }
    }
    .navbar-brand {
        padding: 0;
        float: none;
        height: auto;
        position: relative;
        box-shadow: none;
        margin: 0 !important;  
        @media (max-width: 991px){
            display: table-cell;
            vertical-align: middle;
            text-align: left;
        }
    }
    .navbar-collapse{
        padding-right: 0;
        padding-left: 0;
        float: none;
        text-align: center;
        width: 100%; 
        @media (min-width: 992px){ 
            position: absolute;
            top: 0;
            left: 0;
            pointer-events: none;
            line-height: 90px;
        }
        @media (max-width: 991px){
            width: 100%;
            float: none !important;
            margin: 0;
            border: 0 !important;
            max-height: none;
            text-align: left;
            overflow: hidden;
            background: #26262a;
            box-shadow: none;
        }
    }
    .navbar-nav{
        float: none;
        pointer-events: none;       
        @media (max-width: 991px){
            width: 720px;
            margin: 20px auto 0;
            padding: 0;
        }
        @media (max-width: 767px){
            width: 100%;
            margin: 20px auto 0;
            padding-left: 20px;
            padding-right: 20px; 
        }
        >li {
            float: none;
            pointer-events: all;
            @media (min-width: 992px){
                margin: 0 15px;
                display: inline-block;
                text-align: center; 
            }
            @media (max-width: 991px){
                border: none;
                margin: 0 !important;
            }
            &:first-child{
                >a{
                    border-top: none;
                }
            }
            >a{
                padding: 0;
                font-size: $navbar-font-size;
                font-weight: 700;
                transition: all 150ms ease-in;
                text-transform: uppercase;
                line-height: 1;
                color: $navbar-font-color !important;
                position: relative;  
                @media (max-width: 991px){
                    padding: 12px 0 !important;
                    border-top: 1px solid rgba(255,255,255,0.06);
                }
            }
           >.dropdown-menu {
                margin-top: 0;
                border-top-right-radius: 0;
                border-top-left-radius: 0;
            }
        }
        .dropdown-menu {
            padding: 0;
            margin: 0;
            min-width: 220px;
            border: none;
            background: none;
            border-radius: 0;
            box-shadow: none;
            padding-top: 38px;
            li {
                background: rgba(0, 0, 0, 0.86);
                border: none;
                a{
                    background: none;
                    border: none;
                    color: #fff;
                    font-size: 11px;
                    font-weight: 700;
                    letter-spacing: 0;
                    text-transform: uppercase;
                    padding: 15px 20px 15px;
                    line-height: 1;                  
                }
            }
        }
        .dropdown-toggle{           
            &::after{
                @media (min-width: 992px){
                    display: none;
                }
            }
        }
    }
    .nav-item{
        >a{
            &.nav-link{
                &.active{
                    color: $link-active-color !important 
                }
                &:hover{
                    color: $link-active-color !important 
                }
            }
        }
    }
    .open{
        .dropdown-menu{
            -webkit-animation: fadeInUpMenu 0.4s;
  -moz-animation: fadeInUpMenu 0.4s;
  -ms-animation: fadeInUpMenu 0.4s;
  -o-animation: fadeInUpMenu 0.4s;
  animation: fadeInUpMenu 0.4s;
            @media (max-width: 991px){
                position: static;
                float: none;
                width: auto;
                margin-top: 0;
                background-color: transparent;
                border: 0;
                -webkit-box-shadow: none;
                box-shadow: none;
            }
        }
    }
    .dropdown-menu{
        li{
            a{
                &:hover{
                    background: none;
                    color: $link-active-color;
                }
            }
        }
    }
    .navbar-toggler{  
        color: #fff !important;    
        @media (max-width: 991px){
            display: table-cell;
            vertical-align: middle;
            text-align: right;
        }        
        i{
            &:before{
            content: '\f0c9';
            font-family: 'FontAwesome';
            font-size: 22px;
            display: inline-block;
            font-style: normal;
            font-weight: normal;
            margin-right: 0;
            text-align: center;
            }
        }
        &.opn{
            i{
                &:before{
                    content: '\f00d';
                }
            }
        }
    }
}

@-webkit-keyframes fadeInUpMenu {
  0% {
    opacity: 0;
    -webkit-transform: translateY(10px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}
@keyframes fadeInUpMenu {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.fadeInUpMenu {
  -webkit-animation-name: fadeInUpMenu;
  animation-name: fadeInUpMenu;
}