/*-----------------------------------------------------------------------------------*/
/*  CARD
/*-----------------------------------------------------------------------------------*/

.card{
	background-color: $card-background;
	border-radius: 2px;	
	border: none;
	box-shadow: $card-box-shadow;
	img{
		width: 100%;
		border-radius: 0;
	}
	.card-block{
		padding: 30px;	
		.text-muted{
			margin-bottom: 20px;			
		}		
	}
	.card-title{
		font-size: $card-title-font-size;
		margin-bottom: 5px;
		text-transform: uppercase;		
		a{
			color: #fff;
			&:hover{
				color: $link-active-color;
				text-decoration: none;
			}	
		}
	}
	.card-text{
		color: $card-text-color;
	}
	
}

#shop{
	.card{
		background-color: #fff;
		margin: 5px;
		.card-title{
			margin-bottom: 15px;
		}
		.card-block{
			h4{
				font-size: 14px;			
				font-weight: 500;
				text-transform: uppercase;
				color: #000;
			}
		}
		.btn-shop{
			background-color: #033E8C;
			margin-bottom: 0;
			box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
			border-radius: 2px;
			padding-left: 30px;
			padding-right: 30px;
			&:hover{
				background-color: #0468BF;
				box-shadow: 0 5px 11px 0 rgba(0,0,0,0.18),0 4px 15px 0 rgba(0,0,0,0.15);
			}
		}
	}
}

#players{
	.block-link{
		&:hover{
			.card{
				background-color: #0468BF;
				box-shadow: 0 5px 11px 0 rgba(0,0,0,0.18),0 4px 15px 0 rgba(0,0,0,0.15);
			}
		}
	}
	.card{
		background-color: #033E8C;
		margin: 5px;
		.card-block{
			padding: 10px 30px;
    		border-top: 4px solid #F2B705;
			.media-left{
				padding-right: 20px;
			}
			.person-number{
				font-size: 42px;
				font-weight: 400;
				color: #fff;
				display: block;
			}
			.media-heading{
				font-size: 18px;
				font-weight: 500;
				color: #fff;
				margin-bottom: 5px;
			}
			.media-subtitle{
				font-size: 14px;
				font-weight: 400;
				text-transform: uppercase;
				color: #fff;
				margin-bottom: 0;
			}
		}
	}
}

.products{
	.card{
		margin-bottom: 0;
		.card-block{
			padding: 10px 15px;
		}
	}
}