/*-----------------------------------------------------------------------------------*/
/*  FOOTER
/*-----------------------------------------------------------------------------------*/

footer{
	background: 
    /* top, transparent red, faked with gradient */ 
    linear-gradient(
      rgba(0, 0, 0, 0.3), 
      rgba(0, 0, 0, 0.7)
    ),
    /* bottom, image */
    url('../../assets/img/bg/b9.jpg') no-repeat scroll center center / cover;
	.inner {
	    padding-top: 40px;
	    padding-bottom: 80px;
	}
	.footer-social{
		text-align: center;
		a{
			.fa{
				font-size: 14px;
				height: 35px;
				line-height: 35px;
				margin: 4px;
				width: 35px;
			}
			i{
				background-color: #fff;
				&.icon-round {
				  border-radius: 100%;
				  color: #0d0d0d;
				  margin-right: 13px;
				  text-align: center;
				  vertical-align: middle;
				}
			}			
		}
	}
	.client-wrapper{		
		.client-group{		
			text-align: center;	
			@media (max-width: 767px){
				margin-bottom: 0;
			}
			&:last-child{
				margin-bottom: 0;
			}
			.client-item{
				position: relative;
				vertical-align: middle;
				display: inline-block;
				width: 156px;
				padding: 15px 0;
				border-radius: 2px;
				margin: 30px 15px 0 15px;
				background-color: #fff;
				box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
				@media (max-width: 767px){
					margin: 30px 15px 0 15px;
				}
				@media (max-width: 543px){
					width: 40%;
				}
			}
			
		}
	}
	.sub-footer {
	    background: $primary-color;
	    .inner{
	    	padding-top: 30px;
    		padding-bottom: 30px;
	    }
	}
}