/*-----------------------------------------------------------------------------------*/
/* 	WIDGETS
/*-----------------------------------------------------------------------------------*/

.sidebar{
	padding-left: 35px !important;
	.searchform {
		max-width: 300px;
	}
}
.sidebox{
	display: block;
	&:first-child{
		margin-top: 0;
	}
	a {
	    color: inherit;
	}
}

.widget-title {
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 0;
    line-height: 18px;
    margin-bottom: 15px !important;
}

.searchform {
    position: relative;
    .btn {
		position: absolute;
		top: 0;
		right: -5px;
		height: 40px !important;
		font-weight: 800 !important;
		font-size: 12px !important;
		padding: 0 15px !important;
		margin: 0;
		border-radius: 0;
	}
}

.widget{
	.post-list {
	    h4{
			font-size: 15px;
			line-height: 19px;
			letter-spacing: -0.2px;
			a{
				color: #3b3b3b;
				font-size: 15px !important;
				line-height: 20px;
				text-transform: none !important;
			}
	    }
    	li{
    		margin-bottom: 15px;
    	}
    	.media-body{
    		    font-size: 12px;
    			text-transform: uppercase;
    		em {
			    color: #999999;
			    margin-bottom: 10px;
			    font-style: normal;
			    a{
			    	font-size: 12px;
    				text-transform: uppercase;
			    	color: #999999;
			    }
			}
			span:before {
			    color: #999999;
			}
			span:before {
			    content: "/";
			    padding: 0 10px;
			    color: #b5b5b5;
			}
			span:first-child:before {
    			display: none
			}
    	}
	}
	input {
	    margin-bottom: 0;
	}
	ul {
    	list-style: outside none none;
    	padding-left: 0;
	}
}

ul{
	&.tag-list{
		li {
		    padding: 0;
		    margin: 0;
		    display: inline-block;
		    background: none;
		    a{
				background: #FFF !important;
				color: #3b3b3b !important;
				box-shadow: 0 2px 0 rgba(0, 0, 0, 0.07);
				margin: 0 2px 6px 0;
				position: relative;
				font-weight: 700;
				font-size: 12px !important;
				&:hover {
				    text-decoration: none
				}
		    }
		}
	}
}

