/*-----------------------------------------------------------------------------------*/
/*  SHARE
/*-----------------------------------------------------------------------------------*/

.share{	
	pointer-events: all;
	margin: 0 auto;
	height: 32px;
	img {
		margin-left: 15px;
	}
	.share-link {
		display: inline-block;
		overflow: hidden;
		-webkit-transition: all 0.3s ease-in-out;
		-moz-transition: all 0.3s ease-in-out;
		-ms-transition: all 0.3s ease-in-out;
		-o-transition: all 0.3s ease-in-out;
		transition: all 0.3s ease-in-out;
		position: relative;
		&:before {
			content: '\f1e0';
			font-size: 16px;
			font-family: 'FontAwesome';
			display: inline-block;
			color: #0468BF;
			cursor: pointer; 
			background: $share-background;
			padding: 4px 11px;
		}
		&:hover{
			width: 250px;
			>.share-group{
				display: inline-block;
				left: 35px;
				position: absolute;
				opacity: 1;
				width: 250px;
				top: 0;
				-webkit-transition: all 0.4s ease-in-out;
				-moz-transition: all 0.4s ease-in-out;
				-ms-transition: all 0.4s ease-in-out;
				-o-transition: all 0.4s ease-in-out;
				transition: all 0.4s ease-in-out;
			}
			&:before{
				color: #595959;
				-webkit-transition: all 0.3s ease-in-out;
				-moz-transition: all 0.3s ease-in-out;
				-ms-transition: all 0.3s ease-in-out;
				-o-transition: all 0.3s ease-in-out;
				transition: all 0.3s ease-in-out;
			}
		}
		&:active{
			width: 250px;
			>.share-group{
				display: inline-block;
				left: 35px;
				position: absolute;
				opacity: 1;
				width: 250px;
				top: 0;
				-webkit-transition: all 0.4s ease-in-out;
				-moz-transition: all 0.4s ease-in-out;
				-ms-transition: all 0.4s ease-in-out;
				-o-transition: all 0.4s ease-in-out;
				transition: all 0.4s ease-in-out;
			}
			&:before{
				color: #595959;
				-webkit-transition: all 0.3s ease-in-out;
				-moz-transition: all 0.3s ease-in-out;
				-ms-transition: all 0.3s ease-in-out;
				-o-transition: all 0.3s ease-in-out;
				transition: all 0.3s ease-in-out;
			}
		}
		.share-group {
			display: inline-block;
			opacity: 0;
			overflow: hidden;
			top: 0;
			left: 0;
			width: 250px;
			position: absolute;
			-webkit-transition: all 0.4s ease-in-out;
			-moz-transition: all 0.4s ease-in-out;
			-ms-transition: all 0.4s ease-in-out;
			-o-transition: all 0.4s ease-in-out;
			transition: all 0.4s ease-in-out;
			&:hover{
		    	text-decoration: none;
		    }
			&:first-child{
		    	margin-left: 12px;
		    }			
		    .icon-square{
				background-color: $share-background;
				text-align: center;
				vertical-align: middle;
		    }
		    .fa{
				font-size: $share-font-size;
				color: $share-font-color;
				height: 32px;
				line-height: 32px;
				width: 35px;
			}
		}
		.share-btn {
			padding: 0 10px;
			&:hover{
				text-shadow: 0 0 5px rgba(85, 190, 235, 0.45);
				text-decoration: none;
			}
			&:active{
				@extend :hover;
			}
		}	
	}
}

