#sync1{
	.item {
		text-align: center;
	}
}

#sync2{
	.item {
		margin: 3px;
		text-align: center;
		cursor: pointer;
	}
}

.owl-theme {
	.owl-nav {
		[class*='owl-'] {
			transition: all .3s ease;
			&.disabled:hover {
			background-color: #D6D6D6;
		  }   
		}
	}
}

//arrows on first carousel
#sync1.owl-theme {
	position: relative;
  	.owl-next, .owl-prev {
	    width: 22px;
	    height: 40px;
	    margin-top: -20px;
	    position: absolute;
	    top: 50%;
  	}
  	.owl-next {
    	right: 10px;
  	}
}

#sync1{
	.owl-stage-outer{
		border-radius: 2px;	
		border: none;
		box-shadow: $card-box-shadow;
	}
}

#sync2{
	margin-top: 15px;
	img{
		border-radius: 2px;	
		border: none;
		box-shadow: $card-box-shadow;
	}
}