
/* 1. Colors
   ========================================================================== */

$primary-color: #021F59 !default;
$primary-color-light: #e4e6e7 !default;
$primary-color-dark: #021F59 !default;

$secondary-color: #033E8C !default;
$link-color: #F2B705 !default;
$link-active-color: #F2B705 !default;

/* 2. Fonts
   ========================================================================== */
$roboto-font-path: "/fonts/roboto/" !default;

/* 3. Navigation Bar
   ========================================================================== */
$navbar-font-size: 14px !default;
$navbar-font-color: #fff !default;
$navbar-font-color-active: #F2B705 !default;
$navbar-dropdown-font-size: 13px !default;
$navbar-dropdown-font-color: #333 !default;

/* 4. Buttons
   ========================================================================== */
$button-bg-color: #70aed2 !default;

/* 5. Cards
   ========================================================================== */
$card-background: #0d0d0d !default;
$card-box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12) !default;
$card-title-font-size: 18px !default;
$card-text-color: #82826d !default;

/* 6. Share
   ========================================================================== */
$share-background: #000 !default;
$share-font-size: 14px !default;
$share-font-color: #0468BF !default;